<template>
  <div>
    <el-form :inline="true" :model="formInline" class="demo-form-inline">
      <el-form-item label="审批人">
        <el-input v-model="formInline.user" placeholder="审批人"></el-input>
      </el-form-item>
      <el-form-item label="活动区域">
        <el-select v-model="formInline.pwd" placeholder="活动区域">
          <el-option label="root" value="root"></el-option>
          <el-option label="区域二" value="123"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit">查询</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  export default {
    name: 'trymodel',
    data() {
      return {
        formInline: {
          user: '',
          pwd: ''
        }
      }
    },
    methods: {
      onSubmit() {
        var data = this.formInline
        this.$http.post('/api/login/', data).then(res => {
          alert("=======")
        })
      }
    }

  }
</script>

